import Menunew from 'project-components/Menu/Menu.jsx'
import { disableBodyScroll, clearAllBodyScrollLocks } from 'body-scroll-lock'
import './menuRender.styl'

const menuBtnHandler = () => {
  $('#menu_modal').removeClass('jsx-menu')
  disableBodyScroll(document.querySelector('#menu_modal'))
  $($('html')[0]).addClass('no-scroll')
}

const menuWrapperHandler = e => e.stopPropagation()

const modalWrapHandler = () => {
  $('#menu_modal').addClass('jsx-menu')
  clearAllBodyScrollLocks()
  $($('html')[0]).removeClass('no-scroll')
}

const closeMenu = () => {}

export const menuRender = () => {
  const props = {
    closeMenu: closeMenu
  }
  document.body.appendChild(Menunew(props))
  const menuBtn = $('.more_wrap')[0]
  menuBtn.addEventListener('click', menuBtnHandler)
  const modalWrap = $('#menu_modal')[0]
  const menuWrapper = $('.list-wrap')[0]
  const installButton = $('#install')[0]
  installButton.addEventListener('click', modalWrapHandler)
  menuWrapper.addEventListener('click', menuWrapperHandler)
  modalWrap.addEventListener('click', modalWrapHandler)
}
